.App {
  font-family: sans-serif;
  text-align: center;
}
.grid {
  height: 400px;
  width: 400px;
  border: 1px solid black;
  display: grid;
  grid-template-columns: repeat(10, 40px);
  grid-template-rows: repeat(10, 40px);
}
.box {
  border: 1px solid black;
}
button {
  background: transparent;
  color: blue;
  padding: 5px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
}
button:hover {
  background: blue;
  color: white;
}
